<template>
  <transition name='fade'>
    <div class='collectionPage'>
      <v-Header class="head">
        <span slot='text'>我感兴趣的</span>
      </v-Header>
      <div id='listsScrollCon' class='listsScrollCon'  v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <v-rendezvousList :Lists='Lists' />
        <v-loadmove :type='loadType' v-if='Lists.length>0'/>
        <div class='Tips' v-if='Lists.length==0'>暂无收藏文章</div>
      </div>
    </div>
  </transition>
</template>
<script>
  import Header from '../../components/header';
  import rendezvousList from '../rendezvous/rendezvousList';
  import loadMove1 from '../../components/seeOcean/loadMore.vue';
  export default {
      name: '',
      components: {'v-rendezvousList':rendezvousList,'v-Header':Header,'v-loadmove': loadMove1},
      props: [],
      data () {
        return {
            Lists:[],
            lastProductId:0,
            loading:false,
            loadType:'more'
        }
      },
      methods: {
        gotoPath(_obj){
          
        },
        loadMore(){
          this.getList();
        },
        getList() {
          if(this.loadType == 'all'||this.loading) return;
          this.loading = true;
          this.$http.get(this.$port.MY_CONT_LIKE,{
            params:{
              lastProductId:this.lastProductId
            }
          }).then((response) => {
            const {data} = response;
            this.loading = false;
            if(response.code == 200){
              this.Lists=this.Lists.concat(data);
              if(data.length>0){
                this.loadType = data.length<10?'all':'more';
                this.lastProductId = data.pop().productId;
              }else{
                this.loadType = 'all';
              }
            }
          })
          .catch((error) => {
            this.loading = false;
            console.log(error.response.data);
          });
        },
      },
      created(){
        this.getList();
      }
    }
</script>
<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
.collectionPage{
  width:100%;
  height:100%;
  position: absolute;
  top:0;
  left:0;
  overflow: hidden;
  background:white;
  .listsScrollCon{
    position: absolute;
    top:0.82rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling:touch;
  }
  .Tips{
    text-align: center;
  }
}
</style>


